import * as React from "react";
import { Grid, Hidden } from "@material-ui/core";
import { UIGridProps } from "../../application/ui/CommonUIFactory";

const MuiGrid = ({
  container,
  xs,
  sm,
  md,
  lg,
  xl,
  children,
  style,
  alignItems,
}: UIGridProps): JSX.Element => {
  return container ? (
    <Grid style={{ ...style }} container spacing={0} alignItems={alignItems}>
      {children}
    </Grid>
  ) : (
    <Hidden
      xsDown={xs === false}
      smDown={sm === false}
      mdDown={md === false}
      lgDown={lg === false}
      xlDown={xl === false}
    >
      <Grid style={{ ...style }} item xs={xs} sm={sm} md={md} lg={lg} xl={xl}>
        {children}
      </Grid>
    </Hidden>
  );
};

export default MuiGrid;
